const db = uniCloud.database();
const meal_list = db.collection('shop_meal');
const dbCmd = db.command;
module.exports = {
	_before: function () { // 

	},
	async getMealByShop(id){	//
		//id
		console.log(id,"111")
		let dbJql = uniCloud.databaseForJQL();
		let shop = await dbJql.collection('shop').where({_id:id})
		.getTemp();
		let testShop = await dbJql.collection('shop').where({
			_id:id
		}).get();
		console.log(testShop.data,"2")
		let res = await dbJql.collection(shop,'shop_meal').get()
		.catch((err)=>{
			return {
				code:500,
				msg:"",
				data:null
			}
		});
		if(res.data){
			let type_list = await db.collection('meal_type').where({
				"shop_id":id
			}).get().catch((err)=>{
				return {
					code:500,
					msg:"",
					data:res.data[0]
				}
			});
			if(type_list.data.length>0){
				return {
					code:200,
					msg:",",
					data:{
						meal_data:res.data[0],
						type_data:type_list.data
					}
				}
			}else{
				return {
					code:200,
					msg:",",
					data:{
						meal_data:res.data[0],
						type_data:[]
					}
				}
			}
			
			
		}else{
			return {
				code:500,
				msg:"",
				data:null
			}
		}
	},
	async getMealInfo(id){		//
	//meal_id
		let res = await meal_list.doc(id).get().catch((err)=>{
			return {
				code:500,
				msg:"",
				data:null
			}
		});
		
		if(res.data){
			return {
				code:200,
				msg:"",
				data:res.data
			}
		}else{
			return {
				code:500,
				msg:"",
				data:null
			}
		}
	},
	async getMealByTypeInShop(id,type){		//
		let res = await meal_list.where({
			meal_forShop:id,
			meal_type:type
		}).get().catch((err)=>{
			return {
				code:500,
				msg:"",
				data:null
			}
		});
		
		if(res.data){
			return {
				code:200,
				msg:"",
				data:res.data
			}
		}else{
			return {
				code:500,
				msg:"",
				data:null
			}
		}
	},
	async searchMealInShop(option){
		//option id
		// var option = {
		// 	meal_forShop:"id",
		// 	name:""
		// }
		let res = await db.collection('shop_meal')
		.where({
			meal_forShop:option.meal_forShop,
			meal_name:new RegExp(option.name,'g')
		})
		.get()
		.catch((err)=>{
			return {
				code:500,
				msg:"",
				data:null
			}
		})
		return {
			code:200,
			msg:"",
			data:res.data
		}
	},
	async searchMeal(option){
		//option id
		// var option = {
		// 	name:"",
		//	start:0
		// }
		let dbJql = uniCloud.databaseForJQL();
		if(option.start)option._id = dbCmd.lt(option.start);
		let meal_list = await dbJql.collection('shop_meal')
		.where({
			meal_name:new RegExp(option.name,'g')
		})
		.orderBy("_id","desc")
		.limit(6)
		.getTemp()
		let res = await dbJql.collection(meal_list,'shop').get()
		return {
			code:200,
			msg:"",
			data:res.data
		}
	}
}
