const {baseUrl} = require('../cite')


//codeopenidopenid
module.exports = { 
	getOpenid(code,name,avatar){
		return uni.request({
		url:baseUrl+'/getOpenid',
		data:{
			code,
			name,
			avatar
		},
		method:'GET',
	})
},
	getUserInfo(openid){
		return uni.request({
			url:baseUrl+`/getUserInfo?openid=${openid}`,
			method:'GET'
		})
	}

}